import {HeadFC, PageProps} from "gatsby"
import {StaticImage} from "gatsby-plugin-image"
import {Fragment} from "react"
import Blank from "~/components/Sections/Blank/Blank"
import CaseHistory from "~/components/Sections/CaseHistory/CaseHistory"
import HeroSection from "~/components/Sections/HeroSection/HeroSection"
import News from "~/components/Sections/News/News"
import PrivateArea from "~/components/Sections/PrivateArea/PrivateArea"
import Products from "~/components/Sections/Products/Products"
import RichTextField from "~/components/UI/RichText/RichText"
import SectionContainer from "~/components/UI/SectionContainer/SectionContainer"
import {useLinksQuery} from "~/queries/LinksQuery"
import {SEO} from "../components/UI/SEO/SEO"
import {PageContext} from "../types/PageContext"
import getTextFromHTML from "../utilities/getTextFromHTML"

type HomePageContext = PageContext<Queries.HomePageQuery["allContentfulPaginaHomepage"]["edges"][0]["node"]>

interface HomePageTemplateProps extends PageProps<any, HomePageContext> {}

const HomePageTemplate: React.FC<HomePageTemplateProps> = props => {
    const homePageData = props.pageContext.data

    return (
        <Fragment>
            <HeroSection
                image={homePageData?.header?.gatsbyImageData}
                imageAlt={homePageData?.header?.title}
                mobileImage={homePageData?.headerMobile?.gatsbyImageData}
                position="BOTTOM"
            />
            <SectionContainer withFadeInEffect>
                <Blank>
                    <StaticImage
                        src="./../images/google.png"
                        alt="Google"
                        placeholder="blurred"
                        className="logo-google"
                    />
                    <h3 className="no-margin">{homePageData?.titolo1}</h3>
                    <h3 className="green no-margin">{homePageData?.titolo2}</h3>
                    <RichTextField style={{marginTop: "var(--gutter-5)"}} className="font-h6 chars-h6-80">
                        {homePageData?.paragrafo1?.raw}
                    </RichTextField>
                </Blank>
            </SectionContainer>
            <SectionContainer tabbed hideTabOnMobile title={homePageData?.titoloProdotti} id="products">
                {homePageData?.defaultSlider && (
                    <Products>
                        {homePageData?.defaultSlider?.map((slide, index) => (
                            <Products.Slide
                                cta={slide?.cta}
                                ctaLink={slide?.ctaLink}
                                description={slide?.description}
                                image={slide?.image}
                                phoneImage={slide?.phoneImage}
                                title={slide?.title}
                                key={index}
                            />
                        ))}
                    </Products>
                )}
            </SectionContainer>
            {/* <SectionContainer id="approach" withFadeInEffect>
                <Approach
                    cta={homePageData?.approccio?.cta}
                    linkCta={`/chi-siamo#governance`}
                    titolo={homePageData?.approccio?.titolo}
                    titoloBold={homePageData?.approccio?.titoloBold}
                    paragrafo={homePageData?.approccio?.paragrafo}
                >
                    {homePageData?.approccio?.approcci?.map((approccio, index) => {
                        return (
                            <Approach.Card
                                key={index}
                                title={approccio?.titolo}
                                image={approccio?.image?.gatsbyImageData}
                                imageAlt={approccio?.image?.title}
                                description={approccio?.paragrafo?.raw}
                            />
                        )
                    })}
                </Approach>
            </SectionContainer> */}
            <SectionContainer tabbed hideTabOnMobile title={homePageData?.caseHistory?.titolo} id="case-history">
                {homePageData?.caseHistory && (
                    <CaseHistory>
                        {homePageData?.caseHistory.caseHistories?.map((item, index) => (
                            <CaseHistory.Case
                                key={index}
                                image={item?.image}
                                title={item?.title}
                                description_short={item?.description_short}
                                cta={homePageData?.caseHistory?.ctaName}
                                slug={item?.slug}
                                type={item?.type}
                                customer={item?.customer}
                                project={item?.project}
                            />
                        ))}
                    </CaseHistory>
                )}
            </SectionContainer>
            <SectionContainer id="private-area" withFadeInEffect>
                <PrivateArea
                    title={homePageData?.areaRiservata?.titolo}
                    content={homePageData?.areaRiservata?.paragrafo?.raw}
                    ctaName={homePageData?.areaRiservata?.cta}
                    ctaLink={`/area-riservata`}
                />
            </SectionContainer>
            <SectionContainer id="news">
                {homePageData?.news && (
                    <News>
                        {homePageData?.news.news?.map((newsItem, index) => (
                            <News.NewsItem
                                key={index}
                                title={newsItem?.title}
                                image={newsItem?.image}
                                cta={homePageData?.news?.ctaName}
                                description_short={newsItem?.description_short}
                                date={newsItem?.date}
                                category={newsItem?.category}
                                type={newsItem?.type}
                                slug={newsItem?.slug}
                            />
                        ))}
                    </News>
                )}
            </SectionContainer>
        </Fragment>
    )
}

export default HomePageTemplate

export const Head: HeadFC<any, HomePageContext> = props => {
    const homePageData = props.pageContext.data

    const links = useLinksQuery()

    return (
        <SEO
            title={homePageData?.title ? homePageData?.title : "homepage"}
            description={getTextFromHTML(homePageData?.paragrafo1?.raw)}
            language={props.pageContext.language}
            image={homePageData?.header?.url}
            imageAlt={homePageData?.header?.title}
            siteUrl={`${process.env.SITE_URL}${links.homepage.url}`}
        />
    )
}
