import {GatsbyImage} from "gatsby-plugin-image"
import React from "react"
import {useLinksQuery} from "~/queries/LinksQuery"
import {PostItem} from "~/types/PostItem"
import LinkButton from "../../UI/Button/LinkButton"

import clsx from "clsx"
import "./CaseHistory.css"

interface CaseHistoryProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "children"> {
    children?: React.ReactNode
}

interface CaseHistoryComponents {
    Case: React.FC<CaseCardProps & PostItem>
}

const CaseHistory: React.FC<CaseHistoryProps> & CaseHistoryComponents = props => {
    const {className, children, ...otherProps} = props

    return (
        <div className={clsx("case-history-q24h", className)} {...otherProps}>
            {children}
        </div>
    )
}

export default CaseHistory

interface CaseCardProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title"> {
    cta: string | undefined | null
}

const CaseCard: React.FC<CaseCardProps & PostItem> = props => {
    const {image, title, description_short, cta, slug, className, ...otherProps} = props
    const links = useLinksQuery()

    return (
        <div className={clsx("case-box-q24h", className)} {...otherProps}>
            {image?.gatsbyImageData && image.title && (
                <GatsbyImage alt={image.title} image={image.gatsbyImageData} className="gatsby-image-q24h" />
            )}
            <h5 className="title-q24h">{title}</h5>
            <p className="description-q24h">{description_short}</p>
            <LinkButton to={`${links.caseHistory.url}/${slug}`} className="cta-q24h">
                {cta}
            </LinkButton>
        </div>
    )
}

CaseHistory.Case = CaseCard
